@import "responsive-font-size";
@import "variables";

.queen-lingo-question {
	position: relative;
	h1 {
		text-align: left;
		margin: 0;
		padding: 0;
		margin-left: 20px;
	}
	h3 {
		text-align: left;
		margin: 5px 0 0 30px;
		color: $main-text-color;
		@include responsive-font-size (1em, 2.3em, 640px, 1200px, 0.3em);
		padding: 0;
	}
	.score-float {
		position: absolute;
		top: 60px;
		right: 40px;
		@include responsive-font-size (1em, 1.6em, 640px, 1200px, 0.3em);
	}
	.question {
		@include responsive-font-size (1em, 2em, 640px, 1200px, 0.3em);
		text-align: center;
		margin: 20px 0;
		font-weight: $bolder-text-weight;
	}
	.timer {
		float: right;
		@include responsive-font-size (1em, 1.65em, 640px, 1200px, 0.3em);
		right: 10%;
		position: absolute;
		text-align: center;
	} .lives-count {
		float: left;
		position: absolute;
		color: $main-text-color;
		left: 10%;
		@include responsive-font-size (1em, 1.65em, 640px, 1200px, 0.3em);
		text-align: center;
		i {
			color: $accent-color;
			position: relative;
			top: 4px;
		}
	} .answers {
		margin-top: 80px;
		padding: 20px 20px 0 20px;
		@include responsive-font-size (1em, 1.85em, 640px, 1200px, 0.3em);
		text-align: center;
		p {
			padding: 20px 20px 40px 20px;
			&:hover {
				color: $accent-color;
				cursor: pointer;
				cursor: hand;
				font-weight: 700;
			}
		}
	}
	}

.queen-lingo-text {
	margin: 0 auto;
	text-align: left;
	margin-top: 20px;
	ul {
		padding: 1S0px;
	}
	ul li {
		padding-bottom: 40px;
		max-width: 600px;
		margin: 0 auto;
		color: $main-text-color;
		font-weight: $main-text-weight;
		@include responsive-font-size (1em, 2.3em, 640px, 1200px, 0.3em);
		}
	.arrow {
		margin-right: 20px;
		position: absolute;
		bottom: 50px;
		right: 50px;
	}
}
